import React from 'react'
import Modal from '../Modal/Modal'

const Register = (props) => {
  return (
    <div>
      {props.HandleRegister ? (
        <div onClick={() => props.setHandleRegister()} className="back-drop" />
      ) : null}
      <Modal className="modal" show={props.HandleRegister}>
          <h1>REGISTER</h1>
          <form>
              <div className='form-group'>
                  <input
                    type="text"
                    placeholder='Full Name'
                    className='custom-input'
                    />
              </div>
              <div className='form-group'>
                  <input
                    type="email"
                    placeholder='Email'
                    className='custom-input'
                    />
              </div>
              <div className='form-group'>
                  <input
                    type="password"
                    placeholder='Password'
                    className='custom-input'
                    />
              </div>
              <div className='form-group'>
                  <input
                    type="text"
                    placeholder='User name'
                    className='custom-input'
                    />
              </div>
              <div className='form-group'>
                  <input
                    type="text"
                    placeholder='Phone'
                    className='custom-input'
                    />
              </div>
              <div className='form-submit'>
                  <button
                    className='btn-submit'
                  >
                      REGISTER
                  </button>
              </div>
          </form>
      </Modal>
    </div>
  )
}

export default Register
